import React from 'react';
import slug from 'slug';
import Select from 'react-select';
import ParseReact from 'parse-react';
import { Parse } from 'parse';
import _ from 'lodash';
import Label from '../stories/Label';
import StoryStatusConstants from '../../constants/StoryStatusConstants';

const ParseComponent = ParseReact.Component(React);

function toLabelOption(label) {
  return {
    label: label.name,
    value: slug(label.name),
    type: 'label',
    objectId: label.objectId,
    color: '#D1ABFC'
  };
}

function toStoryOption(story) {
  return {
    label: story.title,
    value: slug(story.title),
    type: 'story',
    objectId: story.objectId,
    color: '#979797'
  };
}

export default class SearchSelect extends ParseComponent {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = {value: []};
  }

  observe(props, state) {
    let storyQuery = new Parse.Query('Story')
      .equalTo('statusRaw', StoryStatusConstants.Public)
      .descending('createdAt')
      .notEqualTo('deleted', true);

    if (state.inputValue) {
      const text = state.inputValue.toLowerCase();
      const words = text.split(' ').filter(w => w !== '');
      storyQuery.matches('title', new RegExp(text, 'i'));
    };

    return {
      labels: new Parse.Query('Label'),
      stories: storyQuery
    };
  }

  handleChange(value) {
    this.setState({value});
    const labels = _(value)
      .filter((option) => option.type === 'label')
      .pluck('objectId')
      .value();
    const stories = _(value)
      .filter((option) => option.type === 'story')
      .pluck('label')
      .value();

    const storiesString = _.reduce(stories, (total, n) => `${total} ${n}`);

    const result = {
      labels,
      stories: storiesString
    };

    this.props.onChange(result);
  }

  handleInputChange(inputValue) {
    this.setState({inputValue});
  }

  renderOption(option) {
    return <span style={{ color: option.color }}>{option.value} </span>;
  }

  // renderValue(option) {
  //   return <span style={{ color: option.color }}>{option.value} </span>;
  // }


  render() {
    const labelOptions = this.data.labels.map(toLabelOption);
    labelOptions.sort((a,b) => a.value.localeCompare(b.value));

    const storyOptions = this.data.stories.map(toStoryOption);
    storyOptions.sort((a,b) => a.value.localeCompare(b.value));

    const allOptions   = labelOptions.concat(storyOptions);

    return(

      <Select
        multi
        value={this.state.value}
        options={allOptions}
        onChange={this.handleChange}
        onInputChange={this.handleInputChange.bind(this)}
        placeholder="Search - labels / story title"
        optionRenderer={this.renderOption}
        // valueRenderer={this.renderValue}
        valueComponent={Label}
      />
    );
  }
}





