import React from 'react';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
import Waypoint from 'react-waypoint';
const ParseComponent = ParseReact.Component(React);
const {Spring} = require('react-motion');
import classNames from 'classnames';

import StoryListItem from './StoryListItem';

const Status = {
  Public: 0,
  Private: 1,
  Open: 2
};


export default class StoryList extends ParseComponent {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      stories: [],
    };
    this.handleWaypointEnter = this.handleWaypointEnter.bind(this);
  }

  observe(props, state) {
    let storyQuery, matchQuery, matchAllQuery;

    storyQuery = this._getBaseQuery();
    if (props.location.query) {
      const labels = props.location.query.labels;
      const stories = props.location.query.stories;
      if (labels) {
        const labelPointers = [].concat(labels).map(function (id) {
          return {
            __type: 'Pointer',
            className: 'Label',
            objectId: id
          };
        });
        storyQuery.containsAll('labels', labelPointers);
      };
      if (stories) {
        const text = stories.toLowerCase();
        const words = this.splitWords(text);

        matchQuery = this._getBaseQuery().matches('title', new RegExp(text, 'i'));
        matchAllQuery = this._getBaseQuery().containedIn('words', words);
        storyQuery = Parse.Query.or(matchAllQuery, matchQuery);
      };
    };

    return {
      stories: storyQuery.skip(state.stories.length),
      bookmarks: new Parse.Query('Bookmark').equalTo('creator', Parse.User.current()).equalTo('typeRaw', 0).include('story'),
      user: ParseReact.currentUser
    };
  }

  componentWillUpdate(nextProps, nextState) {
    super.componentWillUpdate(nextProps, nextState);
    if (nextProps.location.query && this.props.location.query !== nextProps.location.query) {
      this.setState({stories: []});
    };
    if (this.pendingQueries().length < 1 && this.state.isLoading) {
      this.setState({
        isLoading: false,
        stories: this.state.stories.concat(this.data.stories),
      });
    };
  }

  _getBaseQuery() {
    return new Parse.Query('Story').equalTo('statusRaw', Status.Public).include('creator').descending('createdAt').notEqualTo('deleted', true);
  }

  splitWords(text) {
    const words = text.split(' ').filter(w => w !== '');
    return words;
  }

  handleWaypointEnter() {
    this.setState({
      isLoading: true,
    });
  }

  renderBookmarks() {
    return (
      this.data.bookmarks.map((b) => {
        let s = b.story;
        return (
          <StoryListItem
            {...s}
            key={s.id.objectId}
            isBookmarked
          />
        );
      })
    );
  }

  renderPublic() {
    return (
      this.state.stories.map((s) => {
        let bookmarkedStories = this.data.bookmarks.filter((b) => {
          return b.typeRaw == 0 && b.story.id.objectId === s.id.objectId;
        });

        return (
          <StoryListItem {...s}
                          key={s.id.objectId}
                          isBookmarked={bookmarkedStories.length > 0} />
        );
      })
    );
  }

  render() {
    let storyList, stories, waypoint;
    const isBookmark = this.props.history.isActive('/stories/bookmarks');
    const isSearch = this.props.history.isActive('/stories/search');

    if (isBookmark) {
      storyList = this.renderBookmarks();
    } else {
      storyList = this.renderPublic();
    }
    if (!this.pendingQueries().length) {
      waypoint = (
        <Waypoint
          onEnter={this.handleWaypointEnter}
          threshold={0.5}
        />
      );
    };

    let classList = classNames({
      'story-list': true,
      'bookmarks': isBookmark,
      'search-results': isSearch,
      'grid-block': true,
      'small-up-2': true,
      'medium-up-3': true,
      'large-up-4': true,
      'xlarge-up-5': true,
      'xxlarge-up-6': true
    });

    return (
      <div className={classList}>
        {storyList}
        {waypoint}
      </div>
    );
  }
};



