import React from 'react';
import { Link, History } from 'react-router';

const BackButton = React.createClass({
  mixins: [History],
  render() {
    if (this.props.to) {
      return (
        <Link className="back-button" to={this.props.to}>
          <img src={require('../../assets/images/back-arrow.svg')} />
          Back
        </Link>
      );
    } else {
      return (
        <a className="back-button" onClick={this.context.history.goBack}>
          <img src={require('../../assets/images/back-arrow.svg')} />
          Back
        </a>
    );
    }
  }
});

export default BackButton;


